import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { delay, map } from 'rxjs/operators';
import { AuthService } from '../services/auth.service';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {

  constructor(
    private authService: AuthService,
    private router: Router
  ) {}

  canActivate(): Observable<boolean> | boolean {
    console.log('AuthGuard: Verificando acceso...');
    
    // Si ya est치 inicializado, proceder inmediatamente
    if (this.authService.isAuthInitialized()) {
      return this.checkAuth();
    }

    console.log('AuthGuard: AuthService no inicializado, esperando...');
    // Si no est치 inicializado, esperar m치s tiempo y luego verificar
    return of(null).pipe(
      delay(200),
      map(() => {
        console.log('AuthGuard: Reintentando despu칠s del delay...');
        return this.checkAuth();
      })
    );
  }

  private checkAuth(): boolean {
    const isLoggedIn = this.authService.isLoggedIn();
    console.log('AuthGuard: Estado de autenticaci칩n:', isLoggedIn);
    
    if (isLoggedIn) {
      console.log('AuthGuard: Acceso permitido');
      return true;
    } else {
      console.log('AuthGuard: Usuario no autenticado, redirigiendo a login');
      this.router.navigate(['/login']);
      return false;
    }
  }
} 