import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { User } from '../../models/user.model';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-header',
  template: `
    <nav class="navbar navbar-expand-lg navbar-dark bg-primary">
      <div class="container">
        <a class="navbar-brand" href="#" (click)="navigateHome()">
          <i class="fas fa-cloud"></i> ArgCloud
        </a>
        
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav">
          <span class="navbar-toggler-icon"></span>
        </button>
        
        <div class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav ms-auto">
            <li class="nav-item" *ngIf="!isLoggedIn">
              <a class="nav-link" href="#" (click)="navigateLogin()">Iniciar Sesi칩n</a>
            </li>
            <li class="nav-item" *ngIf="!isLoggedIn">
              <a class="nav-link" href="#" (click)="navigateRegister()">Crear Cuenta</a>
            </li>
            <li class="nav-item dropdown" *ngIf="isLoggedIn && currentUser">
              <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown">
                {{ currentUser.firstName }} {{ currentUser.lastName }}
              </a>
              <ul class="dropdown-menu">
                <li><a class="dropdown-item" href="#" (click)="navigateDashboard()">Dashboard</a></li>
                <li><hr class="dropdown-divider"></li>
                <li><a class="dropdown-item" href="#" (click)="logout()">Cerrar Sesi칩n</a></li>
              </ul>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  `,
  styles: [`
    .navbar-brand {
      font-size: 1.5rem;
      font-weight: bold;
    }
    .nav-link {
      font-weight: 500;
      cursor: pointer;
    }
    .dropdown-item {
      cursor: pointer;
    }
  `]
})
export class HeaderComponent implements OnInit, OnDestroy {
  currentUser: User | null = null;
  isLoggedIn: boolean = false;
  private userSubscription: Subscription = new Subscription();

  constructor(
    private authService: AuthService,
    private router: Router,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    console.log('Header: Inicializando...');
    
    // Esperar a que se inicialice el AuthService
    const checkInitialization = () => {
      if (this.authService.isAuthInitialized()) {
        console.log('Header: AuthService inicializado, configurando estado...');
        this.currentUser = this.authService.getCurrentUser();
        this.isLoggedIn = this.authService.isLoggedIn();
        
        console.log('Header: Estado inicial:', { isLoggedIn: this.isLoggedIn, user: this.currentUser });
        
        // Suscribirse a cambios
        this.userSubscription = this.authService.currentUser$.subscribe(user => {
          console.log('Header: Cambio de usuario detectado:', user);
          this.currentUser = user;
          this.isLoggedIn = !!user && this.authService.isLoggedIn();
          this.cdr.detectChanges(); // Forzar detecci칩n de cambios
        });
      } else {
        console.log('Header: AuthService no inicializado a칰n, esperando...');
        setTimeout(checkInitialization, 100);
      }
    };
    
    checkInitialization();
  }

  ngOnDestroy(): void {
    this.userSubscription.unsubscribe();
  }

  navigateHome(): void {
    this.router.navigate(['/']);
  }

  navigateLogin(): void {
    this.router.navigate(['/login']);
  }

  navigateRegister(): void {
    this.router.navigate(['/register']);
  }

  navigateDashboard(): void {
    this.router.navigate(['/dashboard']);
  }

  logout(): void {
    this.authService.logout();
    this.router.navigate(['/']);
  }
} 