import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { delay, map } from 'rxjs/operators';
import { AuthService } from '../services/auth.service';

@Injectable({
  providedIn: 'root'
})
export class GuestGuard implements CanActivate {

  constructor(
    private authService: AuthService,
    private router: Router
  ) {}

  canActivate(): Observable<boolean> | boolean {
    console.log('GuestGuard: Verificando acceso...');
    
    // Si ya est치 inicializado, proceder inmediatamente
    if (this.authService.isAuthInitialized()) {
      return this.checkAuth();
    }

    console.log('GuestGuard: AuthService no inicializado, esperando...');
    // Si no est치 inicializado, esperar m치s tiempo y luego verificar
    return of(null).pipe(
      delay(200),
      map(() => {
        console.log('GuestGuard: Reintentando despu칠s del delay...');
        return this.checkAuth();
      })
    );
  }

  private checkAuth(): boolean {
    const isLoggedIn = this.authService.isLoggedIn();
    console.log('GuestGuard: Estado de autenticaci칩n:', isLoggedIn);
    
    if (!isLoggedIn) {
      console.log('GuestGuard: Usuario no autenticado, acceso permitido');
      return true; // Permitir acceso si NO est치 autenticado
    } else {
      console.log('GuestGuard: Usuario ya autenticado, redirigiendo a dashboard');
      this.router.navigate(['/dashboard']); // Redirigir al dashboard si est치 autenticado
      return false;
    }
  }
} 