import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, BehaviorSubject, tap } from 'rxjs';
import { 
  LoginRequest, 
  RegisterRequest, 
  AuthResponse, 
  MessageResponse, 
  User 
} from '../models/user.model';

const AUTH_API = 'http://localhost:8080/api/auth/';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private currentUserSubject = new BehaviorSubject<User | null>(null);
  public currentUser$ = this.currentUserSubject.asObservable();
  private isInitialized = false;

  constructor(private http: HttpClient) {
    // Usar setTimeout para evitar problemas de inicializaci칩n
    setTimeout(() => {
      this.initializeAuth();
    }, 0);
  }

  private initializeAuth(): void {
    try {
      const storedUser = localStorage.getItem('user');
      const storedToken = localStorage.getItem('token');
      
      console.log('Inicializando AuthService...', { hasUser: !!storedUser, hasToken: !!storedToken });
      
      if (storedUser && storedToken) {
        try {
          const user = JSON.parse(storedUser);
          // Verificar que el token no est칠 obviamente expirado
          if (this.isTokenExpired(storedToken)) {
            console.log('Token expirado detectado durante inicializaci칩n, limpiando sesi칩n...');
            this.clearAuthData();
          } else {
            console.log('Usuario v치lido encontrado, estableciendo sesi칩n...');
            this.currentUserSubject.next(user);
          }
        } catch (error) {
          console.error('Error al parsear usuario almacenado:', error);
          this.clearAuthData();
        }
      } else {
        console.log('No hay datos de sesi칩n almacenados');
      }
    } catch (error) {
      console.error('Error durante inicializaci칩n:', error);
    } finally {
      this.isInitialized = true;
      console.log('AuthService inicializado');
    }
  }

  private isTokenExpired(token: string): boolean {
    try {
      const payload = JSON.parse(atob(token.split('.')[1]));
      const now = Math.floor(Date.now() / 1000);
      const isExpired = payload.exp < now;
      console.log('Verificando expiraci칩n del token:', { exp: payload.exp, now, isExpired });
      return isExpired;
    } catch (error) {
      console.error('Error al validar token:', error);
      return true; // Si no se puede validar, considerarlo expirado
    }
  }

  private clearAuthData(): void {
    console.log('Limpiando datos de autenticaci칩n...');
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    this.currentUserSubject.next(null);
  }

  login(credentials: LoginRequest): Observable<AuthResponse> {
    return this.http.post<AuthResponse>(AUTH_API + 'login', credentials, httpOptions)
      .pipe(
        tap(response => {
          console.log('Login exitoso, guardando datos...');
          this.saveToken(response.token);
          const user: User = {
            id: response.id,
            email: response.email,
            firstName: response.firstName,
            lastName: response.lastName,
            emailVerified: response.emailVerified
          };
          this.saveUser(user);
          this.currentUserSubject.next(user);
        })
      );
  }

  register(userData: RegisterRequest): Observable<MessageResponse> {
    return this.http.post<MessageResponse>(AUTH_API + 'register', userData, httpOptions);
  }

  verifyEmail(token: string): Observable<MessageResponse> {
    return this.http.get<MessageResponse>(AUTH_API + 'verify-email?token=' + token);
  }

  logout(): void {
    console.log('Cerrando sesi칩n...');
    this.clearAuthData();
  }

  public saveToken(token: string): void {
    localStorage.setItem('token', token);
  }

  public getToken(): string | null {
    return localStorage.getItem('token');
  }

  public saveUser(user: User): void {
    localStorage.setItem('user', JSON.stringify(user));
  }

  public getUser(): User | null {
    const user = localStorage.getItem('user');
    return user ? JSON.parse(user) : null;
  }

  public isLoggedIn(): boolean {
    const token = this.getToken();
    const user = this.currentUserSubject.value;
    
    console.log('Verificando estado de login:', { hasToken: !!token, hasUser: !!user, isInitialized: this.isInitialized });
    
    // Si no est치 inicializado, considerarlo como no logueado
    if (!this.isInitialized) {
      console.log('AuthService no inicializado a칰n');
      return false;
    }
    
    // Verificar que existe token, usuario en memoria, y que el token no est칠 expirado
    if (!token || !user) {
      console.log('No hay token o usuario');
      return false;
    }
    
    if (this.isTokenExpired(token)) {
      console.log('Token expirado, limpiando sesi칩n...');
      this.clearAuthData();
      return false;
    }
    
    console.log('Usuario autenticado correctamente');
    return true;
  }

  public getCurrentUser(): User | null {
    return this.currentUserSubject.value;
  }

  public isAuthInitialized(): boolean {
    return this.isInitialized;
  }

  // Funci칩n de debug para limpiar datos corruptos
  public debugClearAll(): void {
    console.log('DEBUG: Limpiando todos los datos de localStorage...');
    localStorage.clear();
    this.currentUserSubject.next(null);
    this.isInitialized = false;
    setTimeout(() => {
      this.initializeAuth();
    }, 100);
  }

  // Funci칩n de debug para mostrar estado actual
  public debugShowState(): void {
    console.log('DEBUG: Estado actual del AuthService:', {
      isInitialized: this.isInitialized,
      currentUser: this.currentUserSubject.value,
      token: this.getToken(),
      storedUser: localStorage.getItem('user'),
      isLoggedIn: this.isLoggedIn()
    });
  }
} 