import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-register',
  template: `
    <div class="container mt-5">
      <div class="row justify-content-center">
        <div class="col-md-6">
          <div class="form-container">
            <h2 class="text-center mb-4">Crear Cuenta</h2>
            
            <form [formGroup]="registerForm" (ngSubmit)="onSubmit()">
              <div class="row">
                <div class="col-md-6 mb-3">
                  <label for="firstName" class="form-label">Nombre</label>
                  <input 
                    type="text" 
                    class="form-control" 
                    id="firstName"
                    formControlName="firstName"
                    [class.is-invalid]="firstName?.invalid && firstName?.touched"
                  >
                  <div class="invalid-feedback" *ngIf="firstName?.invalid && firstName?.touched">
                    <div *ngIf="firstName?.errors?.['required']">El nombre es obligatorio</div>
                  </div>
                </div>

                <div class="col-md-6 mb-3">
                  <label for="lastName" class="form-label">Apellido</label>
                  <input 
                    type="text" 
                    class="form-control" 
                    id="lastName"
                    formControlName="lastName"
                    [class.is-invalid]="lastName?.invalid && lastName?.touched"
                  >
                  <div class="invalid-feedback" *ngIf="lastName?.invalid && lastName?.touched">
                    <div *ngIf="lastName?.errors?.['required']">El apellido es obligatorio</div>
                  </div>
                </div>
              </div>

              <div class="mb-3">
                <label for="email" class="form-label">Email</label>
                <input 
                  type="email" 
                  class="form-control" 
                  id="email"
                  formControlName="email"
                  [class.is-invalid]="email?.invalid && email?.touched"
                >
                <div class="invalid-feedback" *ngIf="email?.invalid && email?.touched">
                  <div *ngIf="email?.errors?.['required']">El email es obligatorio</div>
                  <div *ngIf="email?.errors?.['email']">El formato del email no es v치lido</div>
                </div>
              </div>

              <div class="mb-3">
                <label for="password" class="form-label">Contrase침a</label>
                <input 
                  type="password" 
                  class="form-control" 
                  id="password"
                  formControlName="password"
                  [class.is-invalid]="password?.invalid && password?.touched"
                >
                <div class="invalid-feedback" *ngIf="password?.invalid && password?.touched">
                  <div *ngIf="password?.errors?.['required']">La contrase침a es obligatoria</div>
                  <div *ngIf="password?.errors?.['minlength']">La contrase침a debe tener al menos 8 caracteres</div>
                </div>
              </div>

              <div class="d-grid gap-2">
                <button 
                  type="submit" 
                  class="btn btn-primary"
                  [disabled]="registerForm.invalid || isLoading"
                >
                  <span *ngIf="isLoading" class="spinner-border spinner-border-sm me-2"></span>
                  {{ isLoading ? 'Creando cuenta...' : 'Crear Cuenta' }}
                </button>
              </div>
            </form>

            <div class="alert alert-success mt-3" *ngIf="successMessage">
              {{ successMessage }}
            </div>

            <div class="alert alert-danger mt-3" *ngIf="errorMessage">
              {{ errorMessage }}
            </div>

            <div class="text-center mt-4">
              <p>쯏a tienes una cuenta? 
                <a href="/login" class="text-decoration-none">Iniciar sesi칩n</a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  `,
  styles: []
})
export class RegisterComponent implements OnInit {
  registerForm!: FormGroup;
  isLoading = false;
  errorMessage = '';
  successMessage = '';

  constructor(
    private formBuilder: FormBuilder,
    private authService: AuthService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.registerForm = this.formBuilder.group({
      firstName: ['', [Validators.required]],
      lastName: ['', [Validators.required]],
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(8)]]
    });
  }

  get firstName() { return this.registerForm.get('firstName'); }
  get lastName() { return this.registerForm.get('lastName'); }
  get email() { return this.registerForm.get('email'); }
  get password() { return this.registerForm.get('password'); }

  onSubmit(): void {
    if (this.registerForm.valid) {
      this.isLoading = true;
      this.errorMessage = '';
      this.successMessage = '';

      this.authService.register(this.registerForm.value).subscribe({
        next: (response) => {
          this.isLoading = false;
          this.successMessage = response.message;
          this.registerForm.reset();
        },
        error: (error) => {
          this.isLoading = false;
          this.errorMessage = error.error?.message || 'Error al crear la cuenta';
        }
      });
    }
  }
} 